function main() {
  const postsList = async () => {
    const response = await fetch("../DATA.json");
    const responseJson = await response.json();
    renderAllPostsList(responseJson.restaurants);
  };

  const renderAllPostsList = (listfood) => {
    const postsItemElement = document.querySelector("#postsItem");

    postsItemElement.innerHTML = "";

    listfood.forEach((food) => {
      postsItemElement.innerHTML += `<a href="#" class="card">
        <div class="picture">
          <img src="${food.pictureId}" alt="${food.name}">
          <p class="city-templates">${food.city}</p>
        </div>
        <div class="description">
          <p class="title">${food.name}</p>
          <p class="rating">Rating: ${food.rating}</p>
          <p class="card-description">${food.description}</p>
        </div>
      </a>`;
    });
  };

  document.addEventListener("DOMContentLoaded", () => {
    postsList();
  });
}

export default main;
